<template>
    <section class="right-content">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid border-0 m-0 p-0">
            <div class="w-100">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('AddSMSTemplate') }}</span>
                        <span class="p-actions float-right tablehead">
                            <a class="p-action-btn text-white" href="/SMSTemplate/Index"><i
                                class="fa fa-angle-double-left"></i><br>{{ $t('BacktoList') }}</a>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-sm-12 col-md-6 p-0 float-right bg-light p-4">
            <div class="mobilebase mx-auto">
                <div class="col-12 bg-light p-2 border"><em class="fa fa-bars text-secondary"></em></div>
                <div class="col-12 p-2 bg-light border" style="min-height:350px;">
                    <div class="col-12" id="textcontent">
                        <span v-html="editorData"></span>
                    </div>
                </div>
            </div>
            <div class="col-12 float-left mt-3">
                <div class="input-group">
                    <span class="w-100 centre-flex">
                        <dynamic-form lang="en" ref="AddForm2" :buttons="Testbuttons" :schema="TestFormSchema"
                            v-on:OnSubmit="onTestSubmit"></dynamic-form>
                    </span>
                </div>
            </div>
        </div>
            <dynamic-form ref="AddForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-Content="{ data }">
                    <ckeditor :editor="editor" @ready="onReady" v-model="editorData"></ckeditor>
                    <span name="Content" class="invalid-feedback d-block">{{ data.error }}</span>
                </template>
            </dynamic-form>
    </section>
</template>

<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        GetTemplateData: {
            type: Object,
        },
        item: {
            type: String
        }
    },
    data() {
        return {
            isLoading: false,
            blobPath: 'https://cogniterblob.blob.core.windows.net/',
            attachments: [],
            ProfileId: 0,
            SendThrough: '',
            TwilioAccountSid: '',
            TwilioAuthToken: '',
            TwilioPhoneNumber: '',
            FileName: null,
            CustomValue: [],
            TiggerData: [],
            MobileNumber: '',
            CompanyId: null,
            TEMPLATE_ID: 0,
            ContainerName: null,
            UserId: null,
            ModuleName: 'CRM',
            SubModuleCode: 'CRM_SMS_TEMPLATE',
            fileData: [],
            editor: this.CLASSIC_EDITOR,
            editorData: '',
            Content: '',
            sourceType: null,
            imagePath: '',
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('name'),
                        name: "template_name",
                        value: "",
                        placeholder: "",
                        config: {},
                        validationRules: {
                            "required": true
                        }
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('CustomValue'),
                            name: "CustomValue",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeCustomValueTrigger
                            },
                            validationRules: {
                                "required": false
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('TriggerLink'),
                            name: "TiggerData",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeCustomTrigger
                            },
                            validationRules: {
                                "required": false
                            }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        name: "Content",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        }
                    }, ]
                },
                {
                    layoutType: "single",
                    Data: [{
                            astype: "FileField",
                            label: this.$t('FileControlText'),
                            name: "attachments",
                            value: "",
                            placeholder: "Select",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [{
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpeg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],
                                showSelectedFiles: [],
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                onChange: this.GetBase64String,
                                options: [],
                                multiple: true
                            },
                            validationRules: {
                                "required": false
                            }
                        }
                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onclick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Cancel,
                }
            ],
            TestFormSchema: [{
                    layoutType: "double",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Mobilenumber'),
                        name: "MobileNumber",
                        value: "",
                        placeholder: "Enter Mobile Number",
                        config: {},
                        validationRules: {
                            "required": true
                        },
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('SendThrough'),
                            name: "SendThrough",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [{
                                    name: 'Twilio',
                                    value: 'Twilio'
                                }, {
                                    name: 'Ring Central',
                                    value: 'RingCentral'
                                }],
                                onChange: this.onChangeSendThrough
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SendProfile'),
                            name: "SendProfile",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        }
                    ]
                }
            ],
            Testbuttons: [{
                type: "Send Test",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Send Test",
            }]
        }
    },
    async created() {
        var vm = this;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
        vm.ContainerName = vm.GetUserInfo.ContainerName;
        vm.UserId = vm.GetUserInfo.ID;
        await vm.GetCustomFieldValue();
        await vm.GetTriggerFieldValue();
        if (typeof (vm.GetTemplateData.TEMPLATE_ID) != 'undefined') {
            vm.EditSmsTemplate();
        }
        vm.$refs.AddForm.UpdateKeyValue();
    },
    methods: {
        EditSmsTemplate() {
            var vm = this;
            vm.TEMPLATE_ID = vm.GetTemplateData.TEMPLATE_ID;
            vm.FormSchema[0].Data[0].value = vm.GetTemplateData.TEMPLATE_NAME;
            vm.FormSchema[2].Data[0].value = vm.GetTemplateData.TEMPLATE_CONTENT;
            vm.FormSchema[3].Data[0].value = vm.GetTemplateData.ATTACHMENT_COUNT;
            vm.editorData = vm.GetTemplateData.TEMPLATE_CONTENT;
            var attachmentData = JSON.parse('[' + vm.GetTemplateData.ATTACHMENTS_JSON + ']')
            attachmentData.forEach((item, index) => {
                vm.FormSchema[3].Data[0].config.showSelectedFiles.push({
                    attachmentId: `${index}`,
                    attachmentName: `${item.attachement_name}.${JSON.parse(attachmentData[0].attachement_MIME).Extension}`,
                    attachmentPath: vm.blobPath + '/' + vm.containerName + '/' + item.THUMB_NAIL_PATH,
                })
                vm.attachments.push({
                    value: vm.blobPath + '/' + vm.containerName + '/' + item.THUMB_NAIL_PATH,
                })
            })
        },
        Cancel:function(){          
            window.location.reload();
        },
        onDeleteSelectedFiles: function (item) {
            this.$delete(this.FormSchema[3].Data[0].config, 'showSelectedFiles');
        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        async GetCustomFieldValue() {
            var vm = this;
            var url = 'moduleName=CRM&subModuleCode=CRM_CAMPAIGNS,CRM_CONTACTS';
            await DataServices.GetCustomFieldValue(url).then(function (response) {
                if (response.data != '') {
                    vm.CustomValue = response.data;
                    vm.CustomValue.forEach(item => {
                        vm.FormSchema[1].Data[0].config.options.push({
                            name: item.DISPLAY_NAME,
                            value: item.DISPLAY_NAME
                        })
                    })
                }
            });
        },
        async GetTriggerFieldValue() {
            var vm = this;
            var url = 'type=CRM_TRIGGER&moduleName=null';
            await DataServices.GetTriggerFieldValue(url).then(function (response) {
                if (response.data != '') {
                    vm.TiggerData = response.data;
                    vm.TiggerData.forEach(item => {
                        vm.FormSchema[1].Data[1].config.options.push({
                            name: item.NAME,
                            value: item.LINK
                        })
                    })
                }
            })
        },
        onChangeCustomTrigger(e) {
            var vm = this;
            $('#template_content .form-control').focus();
            if (e.target.value != '') {
                vm.editorData += e.target.value + '?_id=@id';
            }
        },
        // onChangeCustomTrigger(e) {
        //     debugger
        //     var vm = this;
        //     var html = "";
        //     if(vm.editor){
        //         vm.TiggerData.forEach((item, index)=> {
        //             if(e.target.value != '' && e.target.value == item.LINK){
        //                 html = e.target.value + '?_id=' + item.ID + '';
        //             }
        //         }) 
        //         debugger
        //         vm.editor.model.change(writer => {
        //             const insertionPosition = vm.editor.model.document.selection.getFirstPosition();
        //             writer.insertText(html, insertionPosition);
        //         })
        //     }
        // },
        onChangeCustomValueTrigger(e) {
            var vm = this;
            $('#template_content .form-control').focus();
            if (e.target.value != '') {
                vm.editorData += '@'+ e.target.value +'@';    
            }
        }, 
        // onChangeCustomValueTrigger(e) {
        //     debugger
        //     var vm = this;
        //     var html = "";
        //     if(vm.editor){
        //         vm.CustomValue.forEach((item, index) => {
        //             if(e.target.value != '' && e.target.value == item.DISPLAY_NAME){
        //                 html = '@' + e.target.value + '@';
        //             }
        //         })
        //         debugger
        //         vm.editor.model.change(writer => {
        //             const insertionPosition = vm.editor.model.document.selection.getFirstPosition();
        //             writer.insertText(html, insertionPosition);
        //         })
        //     }
        // },
        async onChangeSendThrough(event) {
            debugger
            var vm = this;
            vm.ProfileId = 0;
            if (typeof (event) == 'object') {
                vm.SendThrough = event.target.value;
            } else {
                vm.SendThrough = event;
            }
            vm.TestFormSchema[1].Data[1].value = '';
            await vm.FillProfileDropdown(vm.SendThrough);
            vm.$refs.AddForm2.UpdateKeyValue();
        },
        onChangeProfile(event) {
            var vm = this;
            vm.ProfileId = event.target.value;
            vm.GetEmailProfile(vm.ProfileId);
            vm.$refs.AddForm2.UpdateKeyValue();
        },
        async FillProfileDropdown(type) {
            var vm = this;
            vm.TestFormSchema[1].Data[1].config.options = [];
            var url = `id=&moduleName=CRM&type=${type}`;
            await DataServices.GetDDLData(url + "&search=").then(response => {
                if (response.data.DATA != undefined) {
                    var options = response.data.DATA;
                    options.forEach(item => {
                        vm.TestFormSchema[1].Data[1].config.options.push({
                            name: item.name,
                            value: item.value
                        });
                    })
                }
            });
            vm.$refs.AddForm2.UpdateKeyValue();
        },
        async GetEmailProfile(ths) {
            var vm = this;
            vm.TwilioAccountSid = '';
            vm.TwilioAuthToken = '';
            vm.TwilioPhoneNumber = '';
            var url = `profileId=${vm.ProfileId}&sourceType=`;
            await DataServices.GetEmailProfile(url).then(response => {
                if (response.data != '') {
                    if (typeof (response.data) == 'object') {
                        var IntegrationData = JSON.parse(Object.values(response.data)[0])[0];
                    }
                    vm.TwilioAccountSid = IntegrationData.key;
                    vm.TwilioAuthToken = IntegrationData.SecretKey;
                    vm.TwilioPhoneNumber = IntegrationData.from_email;
                }
            });
        },
        async onTestSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            var formData = JSON.stringify(vm.fileData);
            if (vm.fileData.length > 0) {
                await DataServices.UploadFile(formData).then(function (response) {
                    if (response.data != '') {
                        var Image = response.data;
                        Image.forEach((item,index) => {
                            if(index==0){
                                vm.imagePath = item.Name;
                            }else{
                                vm.imagePath += ',' + item.Name;
                            }
                        })
                    }
                })
            }

            var Content = vm.removeHTMLTags(vm.editorData);
            var cleanStr = Content.replace(/&nbsp;/g, '').replace(/\s+/g, ' ');

            var obj = {
                Send_sms: cleanStr,
                Send_to: formfields.MobileNumber,
                image_path: vm.imagePath,
                TwilioAccountSid: vm.TwilioAccountSid,
                TwilioAuthToken: vm.TwilioAuthToken,
                TwilioPhoneNumber: vm.TwilioPhoneNumber
            }
            var url = JSON.stringify(obj);
            await DataServices.SendTwilioSMS(url).then(function (response) {
                if (response.data == 'queued' && (response.statusText == 'OK' || response.status == 200)) {
                    vm.ShowAlert(vm.$t([vm.$t('MessageSend')]), "success", true, vm.$t("Alert"));
                } else if (response.data == '' && (response.statusText == 'OK' || response.status == 200)) {
                    vm.ShowAlert(vm.$t([vm.$t('InvalidNumber')]), "warning", true, vm.$t("Alert"));
                } else {
                    vm.ShowAlert(vm.$t('SendSMSError', [vm.$t('Message')]), "failure", true, vm.$t("Alert"));
                }
            })
            vm.isLoading = false;
        },
        async onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            var TemplateContent = vm.editorData;
            if (TemplateContent.includes("<p>")) {
                vm.editorData = TemplateContent.replace(/<p>/g, "").replace(/<\/p>/g, "").replace(/&nbsp;/g, '').replace(/\s+/g, ' ');
            } else {
                vm.editorData = vm.editorData;
            }
            var obj = {
                template_id: vm.TEMPLATE_ID,
                template_name: formfields.template_name,
                template_content: vm.editorData,
            }
            var url = JSON.stringify(obj);
            await DataServices.ManageSMSCampaign(url).then(async function (response) {
                    vm.isLoading = false;
                    if (response.data.MSG == 'INSERT') {
                        if (response.data.MAIN_ID > 0) {
                            if (response.data.MAIN_ID > 0) {
                                if (vm.fileData.length > 0) {
                                    vm.fileData.forEach(x => {
                                        x.id = response.data.MAIN_ID;
                                    })
                                }
                                var formData = JSON.stringify(vm.fileData);
                                await DataServices.UploadFile(formData).then(function (response) {})
                            }
                        }
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Campaign')]), "Success", true, vm.$t("Alert"));
                        window.location.reload();
                    } else if (response.data.MSG == 'UPDATE') {
                        if (response.data.MAIN_ID > 0) {
                            if (response.data.MAIN_ID > 0) {
                                if (vm.fileData.length > 0) {
                                    vm.fileData.forEach(x => {
                                        x.id = response.data.MAIN_ID;
                                    })
                                }
                                var formData = JSON.stringify(vm.fileData);
                                await DataServices.UploadFile(formData).then(function (response) {})
                            }
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Campaign')]), "Success", true, vm.$t("Alert"));
                            window.location.reload();
                        }
                    } else if (response.data.MSG == 'ALREADY_EXISTS') {
                        vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Campaign')]), "warning", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t('AddedError', [vm.$t('Campaign')]), "failure", true, vm.$t("Alert"));
                    }
                })
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];

                const files = evt.target.files;

                for (let i = 0; i < files.length; i++) {
                    const file = files[i];
                    const reader = new FileReader();

                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            var base64StringEncoded = window.btoa(binaryData);

                            var fileName = theFile.name;
                            vm.fileName = fileName;

                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + theFile.type + '","Size":"' + theFile.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = theFile.size;
                            vm.fileSize = fileSize;

                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: theFile.name,
                                base64String: base64StringEncoded,
                                fileSize: theFile.size,
                                contentType: theFile.type,
                                moduleName: 'CRM',
                                subModuleCode: 'CRM_SMS_TEMPLATE',
                                containerName: vm.ContainerName,
                                companyId: vm.CompanyId,
                                userId: vm.UserId
                            };
                            vm.fileData.push(fileObj);
                        };
                    })(file);

                    reader.readAsBinaryString(file);
                }
            } catch (ex) {
                console.error(ex);
            }
        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
        removeHTMLTags(text) {
          return text.replace(/<[^>]*>/g, '');
        }
    }
}
</script>
