<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-2 mt-2">
                <LeftColumn />
            </div>
            <div class="col-10" v-if="isShowList">
                <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="CRM" SubModuleCode="CRM_SMS_TEMPLATE"
                    IdentityColumn="TEMPLATE_ID" :HeaderText="$t('SMSTemplate')" :ListData="TemplateData" :HeaderData="Headers"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" :IsSearchFieldsOnTop="false"
                    SearchPosition="top" :SearchFields="topSearchFields"
                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" 
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">+

                    <template v-slot:slotdata="{ data }">
                      <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'"> 
                            <listing-status :isfor="'CRM_SMS_TEMPLATE'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'COMPANYSETUP_UPDATE')"
                                ref="updatestatus" v-bind:dataid="data.row.TEMPLATE_ID" data-controller="COMPANYSETUP_UPDATE"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.row" data-action="index" module="CRM" subModule="CRM_SMS_TEMPLATE"
                                v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                            </listing-status>
                        </template>
                    </template>

                </tg-list>
            </div>
            <modalManageSMSTemplate v-if="ShowmodalManageSMSTemplate" :GetTemplateData="GetTemplateData" @close="ShowmodalManageSMSTemplate = false"></modalManageSMSTemplate>
        </div>
    </div>
</template>

<script>
import LeftColumn from '../LeftColumn.vue';
import Schema from '../../../views/Company/SMSTemplate/SMSTemplateSchema'
import DataService from '../../../services/DataService'
import modalManageSMSTemplate from '../../../components/Modals/modalManageSMSTemplate.vue';
export default {
    components: {
        LeftColumn,
        modalManageSMSTemplate
    },
    data() {
        return {
            isShowList: true,
            ShowmodalManageSMSTemplate: false,
            isLoading: false,
            TemplateData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            topSearchFields: Schema.topsearchSchema,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            templateName: '',
            privilegeParams: null,
            TotalPages: 0,
            CurrentPage: 1,
            searchCondition: '',
            GetTemplateData: {},
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
            Headers: [{
                    COLUMN_NAME: "TEMPLATE_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "TEMPLATE_CONTENT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Description",
                    DISPLAY_ORDER: 2
                },
                {
                    COLUMN_NAME: "STATUS_ID",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "TYPE",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Type",
                    DISPLAY_ORDER: 4
                },
                {
                    COLUMN_NAME: "ATTACHMENT_COUNT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Attachment",
                    DISPLAY_ORDER: 5
                }
            ]
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        vm.privilegeParams = `controller=SMSTemplate&action=`;
        await vm.getStatuslistForListing("Dynamic_Reporting", "en");
        vm.Headers.forEach((column) => {
        column.settings = null;
            if (column.COLUMN_NAME == 'STATUS_ID') {
                column.settings = {
                    isInSlot: true,
                    isForTag: true
                }
            }
            if (column.COLUMN_NAME == "TEMPLATE_NAME") {
                    column.settings = {
                    clickEvent: vm.clickEventOfColumn,
                    formatter: vm.columnDataFormatter,
                }
            }                        
        });
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        modalManageSMSTemplate: function () {
            this.ShowmodalManageSMSTemplate = true;
            this.isShowList = false;
        },
        ClosemodalManageSMSTemplate: function () {
            this.ShowmodalManageSMSTemplate = false;
        },
        clickEventOfColumn: function (row, column) {      
            var vm=this;
            if (column.COLUMN_NAME == 'TEMPLATE_NAME') {
                if (row.TEMPLATE_ID > 0) {
                    vm.reqType = 'Update';
                    vm.GetTemplateData = row;
                    vm.ShowmodalManageSMSTemplate = true;
                    vm.isShowList = false;
                } else {
                    vm.GetTemplateData = {};
                    vm.ShowmodalManageSMSTemplate = false;
                    vm.isShowList = false;
                }
           }
        },
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "modalManageSMSTemplate":
                    this.modalManageSMSTemplate();
                break;
                case "commonDeleteData":
                    this.commonDeleteData();
                break;
            }
        },
        renderActions: function (row) {
            console.log(row);
        },
        actionButtonClick: function (item) {
            console.log(item);
        },
        getStatuslistForListing: async function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            await DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            await this.FetchData();
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true; 
            var url = `templateName=${vm.templateName}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&searchQuery=${vm.searchCondition.trim()}`; 
            await DataService.SMSTemplateListing(url).then(function (response) {
                vm.isLoading=false;
                if (typeof (response.data.data) != 'undefined' && response.data.data != null){
                    vm.TemplateData = response.data.data;
                    vm.TotalRecord = response.data.data[0].Total;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else 
                {
                    vm.TemplateData = [];
                    vm.TotalRecord = 0;
                }
            });
            setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;  
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "CRM",
                        subModuleCode: "CRM_SMS_TEMPLATE",
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonDeleteData(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Records')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            await vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Records')]), "failure", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        }
    }
}
</script>
